<template>
  <section class="publish-car-info c-gap-inner-header c-container">
    <div
      class="c-font-20 c-gap-top-20">
      
    </div>
    <el-form
      class="c-gap-top-20">
      <el-row>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            label="">
            <el-select
              :value="carType.value"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleTypeChange">
              <el-option
                v-for="(item2, index2) in carType.options"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            label="">
            <el-select
              filterable
              allow-create
              v-model="price.value"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handlePriceChange">
              <el-option
                v-for="(item2, index2) in price.options"
                :key="index2"
                :label="item2.label"
                :value="item2.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            label="">
            <el-select
              filterable
              allow-create
              v-model="mileage.value"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleMileageChange">
              <el-option
                v-for="(item2, index2) in mileage.options"
                :key="index2"
                :label="item2.label"
                :value="item2.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            :label="interiorColor.text">
            <el-select
              :value="interiorColor.value"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleInteriorColorChange">
              <el-option
                v-for="(item2, index2) in interiorColor.options"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            :label="exteriorColor.text">
            <el-select
              :value="exteriorColor.value"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleExteriorColorChange">
              <el-option
                v-for="(item2, index2) in exteriorColor.options"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            label="">
            <el-select
              :value="$store.state.curBrand.get('label')"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleBrandsChange">
              <el-option
                v-for="(item2, index2) in $store.state.brands.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            label="">
            <el-select
              :value="$store.state.curSerie.get('label')"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleSeriesChange">
              <el-option
                v-for="(item2, index2) in $store.state.series.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            label="">
            <el-select
              :value="$store.state.curSubserie.get('label')"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleSubseriesChange">
              <el-option
                v-for="(item2, index2) in $store.state.subseries.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col
          :span="6">
          <el-form-item
            label="">
            <el-select
              :value="$store.state.curModel.get('label')"
              placeholder=""
              :style="{
                'width': '100%'
              }"
              @change="handleModelsChange">
              <el-option
                v-for="(item2, index2) in $store.state.models.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <!--  -->
        <el-col
          :span="24">
          <el-form-item
            label="17">
            <el-input
              :maxlength="17"
              :minlength="17"
              v-model="vin"
              placeholder="">
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <!--  -->
      <el-row>
        <el-col
          :span="24">
          <el-form-item
            class="c-gap-bottom-zero"
            label="2MB">
          </el-form-item>
        </el-col>
        <el-col
          :span="24">
          <el-upload
            ref="photoUpload"
            action="https://jsonplaceholder.typicode.com/posts/"
            multiple
            list-type="picture-card"
            :auto-upload="false"
            :on-preview="handlePhotoPreview"
            :on-remove="handlePhotoRemove"
            :on-success="handlePhotoSuccess"
            :on-error="handlePhotoError"
            :on-progress="handlePhotoProgress"
            :before-upload="handlePhotoBeforeUpload"
            :before-remove="handlePhotoBeforeRemove">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-col>
      </el-row>
      <!--  -->
      <el-row>
        <el-col
          :span="24">
          <el-form-item
            class="c-gap-bottom-zero"
            label="2MB">
          </el-form-item>
        </el-col>
        <el-col
          :span="24">
          <el-upload
            ref="vinPhotoUpload"
            action="https://jsonplaceholder.typicode.com/posts/"
            multiple
            list-type="picture-card"
            :auto-upload="false"
            :on-preview="handlePhotoPreview"
            :on-remove="handlePhotoRemove"
            :on-success="handlePhotoSuccess"
            :on-error="handlePhotoError"
            :on-progress="handlePhotoProgress"
            :before-upload="handlePhotoBeforeUpload"
            :before-remove="handlePhotoBeforeRemove">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-col>
      </el-row>
      <el-row
        class="c-gap-top-20">
        <el-col
          class="flex-center"
          :span="24">
          <el-button
            type="success"
            @click="handleSubmit">
            
          </el-button>
        </el-col>
      </el-row>
    </el-form>
  </section>
</template>

<script>
export default {
  name: "PublishCarInfo",
  data() {
    return {
      // 
      carType: {
        key: "type",
        text: "",
        value: 0,
        options: [
          {
            label: "&",
            value: 0
          },
          {
            label: "",
            value: 1,
          },
          {
            label: "",
            value: 2
          },
          {
            label: "",
            value: 3
          }
        ]
      },
      price: {
        key: "price",
        text: "",
        value: 100000,
        options: [
          {
            value: 50000
          },
          {
            value: 100000,
          },
          {
            value: 200000
          },
          {
            value: 300000
          },
          {
            value: 500000
          },
          {
            value: 1000000
          }
        ]
      },
      // 
      mileage: {
        key: "mileage",
        text: "",
        value: 100,
        options: [
          {
            value: 500
          },
          {
            value: 1000,
          },
          {
            value: 2000
          },
          {
            value: 3000
          },
          {
            value: 5000
          },
          {
            value: 10000
          }
        ]
      },
      // 
      interiorColor: {
        key: "interior_color",
        text: "",
        value: 0,
        options: [
          {
            label: "",
            value: 0
          },
          {
            label: "",
            value: 1,
          },
          {
            label: "",
            value: 2
          },
          {
            label: "",
            value: 11
          }
        ]
      },
      // 
      exteriorColor: {
        key: "exterior_color",
        text: "",
        value: 0,
        options: [
          {
            label: "",
            value: 0
          },
          {
            label: "",
            value: 1,
          },
          {
            label: "",
            value: 2
          },
          {
            label: "",
            value: 14
          }
        ]
      },
      // 
      vin: ''
    };
  },
  components: {},
  created() {
    // 
    this.$store.dispatch("getBrands", {
      vm: this
    });
  },
  mounted() {},
  methods: {
    // 
    handleTypeChange(index) {
      this.carType.value = this.carType.options[index].value;
    },
    // 
    handlePriceChange(value) {
    },
    // 
    handleMileageChange(value) {
    },
    // 
    handleInteriorColorChange(index) {
      this.interiorColor.value = this.interiorColor.options[index].value;
    },
    // 
    handleExteriorColorChange(index) {
      this.exteriorColor.value = this.exteriorColor.options[index].value;
    },
    // 
    handleBrandsChange(brandIndex) {
      const brandItem = this.$store.state.brands.toJS().data[brandIndex];
      console.log("brandItem", brandItem);
      this.$store.commit("setStateData", {
        ...brandItem,
        stateName: "curBrand"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curSerie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curModel"
      });
      this.$store.dispatch("getSeries", {
        brandId: brandItem.id,
        vm: this
      });
    },
    // 
    handleSeriesChange(seriesIndex) {
      const seriesItem = this.$store.state.series.toJS().data[seriesIndex];
      this.$store.commit("setStateData", {
        ...seriesItem,
        stateName: "curSerie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curModel"
      });
      this.$store.dispatch("getSubseries", {
        brandId: this.$store.state.curBrand.toJS().value,
        seriesId: seriesItem.id,
        vm: this
      });
    },
    // 
    handleSubseriesChange(subseriesIndex) {
      const subseriesItem = this.$store.state.subseries.toJS().data[subseriesIndex];
      this.$store.commit("setStateData", {
        ...subseriesItem,
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curModel"
      });
      this.$store.dispatch("getModels", {
        brandId: this.$store.state.curBrand.toJS().value,
        seriesId: this.$store.state.curSerie.toJS().value,
        subseriesId: subseriesItem.id,
        vm: this
      });
    },
    // 
    handleModelsChange(modelIndex) {
      const modelItem = this.$store.state.models.toJS().data[modelIndex];
      this.$store.commit("setStateData", {
        ...modelItem,
        stateName: "curModel"
      });
    },
    handlePhotoPreview() {
      console.log('handlePhotoPreview');
    },
    handlePhotoRemove() {
      console.log('handlePhotoRemove');
    },
    handlePhotoSuccess() {
      console.log('handlePhotoSuccess');
    },
    handlePhotoError() {
      console.log('handlePhotoError');
    },
    handlePhotoProgress() {
      console.log('handlePhotoProgress');
    },
    handlePhotoBeforeUpload() {
      console.log('handlePhotoBeforeUpload');
    },
    handlePhotoBeforeRemove() {
      console.log('handlePhotoBeforeRemove');
    },
    handleVinPhotoPreview() {
      console.log('handleVinPhotoPreview');
    },
    handleVinPhotoRemove() {
      console.log('handleVinPhotoRemove');
    },
    // 
    handleSubmit() {
      // 
      const data = {};
      // 
      data.type = this.carType.value;
      // 
      data.price = this.price.value;
      // 
      data.mileage = this.mileage.value;
      // 
      data.interior_color = this.interiorColor.value;
      // 
      data.exterior_color = this.exteriorColor.value;
      // 
      data.model_id = this.$store.state.curModel.get('value');
      if (!data.model_id) {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
        return;
      }
      // 
      data.vin = this.vin;
      if (!data.vin) {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
        return;
      }
      if (data.vin.length !== 17) {
        this.$message({
          showClose: true,
          message: '17',
          type: 'error'
        });
        return;
      }
      // 
      const uploadPhoto = this.$refs.photoUpload;
      if (uploadPhoto.uploadFiles.length === 0) {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
        return;
      }
      data.photo = uploadPhoto.uploadFiles[0].raw;
      // 
      const vinPhotoUpload = this.$refs.vinPhotoUpload;
      if (vinPhotoUpload.uploadFiles.length === 0) {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
        return;
      }
      data.vin_photo = vinPhotoUpload.uploadFiles[0].raw;
      console.log(' data', data);
      this.$store.dispatch("uploadCarInfo", {
        ...data,
        vm: this
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.flex-center {
  display: flex;
  justify-content: center;
  align-content: center;
}

.publish-car-info {

  .filter-item {
    display: inline-block;
  }

  .divider {
    margin-bottom: 20px;
    height: 1px;
    background: $grey200;
  }

  .btn-layout {
    display: flex;
    justify-content: center;

    .btn-right {
      margin-left: 100px;
    }
  }
}
</style>



