<template>
  <section
    class="car-card"
    @click.stop="handleClick">
    <!---->
    <div
      class="left-img"
      :style="{
        'background-image': 'url('+ imgSrc +')'
      }">
    </div>
    <!---->
    <section
      class="middle c-gap-inner-left-20 c-gap-inner-right-20"
      :style="{
        'width': !isCustom ? '100%' : ''
      }">
      <h2 class="row1">
        {{item.type.value}}
        {{item.model.year}}
        {{item.model.subseries.series.brand.name}}
        {{item.model.name}}
        {{item.model.arrangement}}
        {{item.model.subseries.name}}
        {{item.model.subseries.series.name}}
      </h2>
      <div class="row2">
        <!--  -->
        <span class="c-font-big">{{item.price}}</span>
      </div>
      <div class="row4 c-gap-top-20">
        <span>
          <span class="c-color-grey"></span>
          {{item.mileage}}
        </span>
      </div>
      <div class="row5">
        <span>
          <span class="c-color-grey"></span>
          {{item.exterior_color.value}}
        </span>
        <span>
          <span class="c-color-grey"></span>
          {{item.interior_color.value}}
        </span>
      </div>
      <div class="row6">
        <span>
          <span class="c-color-grey"></span>
          {{item.model.kpl}}L
        </span>
      </div>
      <!---->
      <div
        v-if="isCustom"
        class="star c-btn"
        @click="handleStarClick">
        <i
          v-if="isStar"
          class="el-icon-star-on">
        </i>
        <i
          v-if="!isStar"
          class="el-icon-star-off">
        </i>
        
      </div>
    </section>
    <!---->
    <section
      v-if="isCustom"
      class="right c-gap-inner-left-20 c-gap-inner-right-20">
      <div class="c-gap-bottom-20">{{item.dealer.company_name}} {{item.dealer.address}}</div>
      <div>{{item.dealer.phone}}</div>
    </section>
  </section>
</template>

<script>
export default {
  name: "CarCard",
  props: {
    isCustom: {
      type: Boolean,
      default: true
    },
    item: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      // 
      mockData: {
        id: "18d5d3eb6741467cb16be739e9f01409",
        // 
        model: {
          id: 208,
          // 
          subseries: {
            id: 77,
            // 
            series: {
              id: 35,
              // 
              brand: {
                id: 5,
                name: "",
                eng_name: null // 
              },
              name: ""
            },
            name: "1.6L", // 
            body_style: 1,
            tank: "55.0", // L
            // 
            height: 1460, // 
            length: 4605, // 
            width: 1765, // 
            axle: 2610 // 
          },
          name: "",
          engine: "1.6", // L
          turbo: false, // 
          arrangement: "L4", // L4V6W12V8
          year: 2018, // 
          transmission: 0, // /
          drivetrain: 0, // 
          fuel: 0, // 
          seating: 5, // 
          door: "4", // 
          msrp: 0, // 0
          kpl: "4.9", // 
          tire: "", // R17 255/255
          // 
          interior: null, // 
          exterior: null, // 
          safety: null, // 
          control: null, // 
          option: null, // 
          url:
            "http://brand.vw.com.cn/d5carsetting/html/132/196/configuration.html" // 
        },
        // 
        dealer: {
          user: {
            id: 2,
            email: "johnny@gmail.com",
            address: null,
            phone: null,
            photo: null
          },
          company_name: "",
          review: "0.0", // 
          address: "68",
          phone: "12344442222",
          photo: null
        },
        vin: "4JGDF6EE6HA961162", // id
        exterior_color: 0, // 
        interior_color: 2, // 
        is_import: false, // 
        photo: [], // 
        type: 0, // 
        price: 230000, // 
        mileage: 50, // 
        created_at: "2018-04-09T15:47:41.859681+08:00",
        updated_at: "2018-04-09T15:47:41.859712+08:00",
        sold_at: null, // 
        stock: null, // 
        description: "", // 
        pending: false, // 
        available: true, // 
        // 
        tire: null, // R17 255/255
        interior: [], // 
        exterior: [], // 
        safety: [], // 
        control: [], // 
        feature: [] // 
      },
      // 
      defaultImgSrc:
        "http://img.hb.aicdn.com/aaab09042b57e0a1def2b0db78b0d0a724a0e38d15c47a-WdWpa0_fw658",
      title: " 2016- CC 1.8T",
      price: "17.70",
      officialPrice: "18.9", // 
      distance: "5", // 
      isStar: false
    };
  },
  computed: {
    imgSrc() {
      if (this.item && this.item.photo && this.item.photo[0] && this.item.photo[0].file) {
        return this.item.photo[0].file;
      }
      return this.defaultImgSrc;
    }
  },
  methods: {
    handleStarClick() {
      this.isStar = !this.isStar;
      this.$emit("onStarClick");
    },
    handleClick() {
      this.$emit("click");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.car-card {
  display: flex;
  padding: 20px 0 20px 20px;
  border: 1px solid $secondColor;
  background: #fff;
  cursor: pointer;

  .left-img {
    background: center no-repeat;
    background-size: cover;
    width: 280px;
    height: 160px;
  }

  .middle {
    position: relative;
    width: 300px;

    .row2,
    .row5,
    .row4,
    .row6 {
      display: flex;
      justify-content: space-between;
    }

    .star {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      top: -12px;
      right: -12px;
      background: #fff;
      font-size: 12px;

      & i {
        font-size: 18px;
      }
    }
  }

  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-left: 1px solid $secondColor;
  }
}
</style>



