import Vue from 'vue';
import Vuex from 'vuex';
import {fromJS} from 'immutable';
import httpConfig from "../configs/httpConfig"; // http 
import axios from "axios"; // 
import Qs from "qs"; //  query string
import StoreJs from "store"; //  js
import {storageConstant} from "../constant.js"; // 

//  Vuex
Vue.use(Vuex);

//  LocalStorage/Cookies 
const loginInfo = StoreJs.get(storageConstant.loginInfo);
console.log('store initial loginInfo', StoreJs.get(storageConstant.loginInfo));

//  Vue.use(Vuex)
export default new Vuex.Store({
  /**
   * state
   */
  state: {
    // 
    loginInfo: fromJS({
      isLoading: false, // 
      token: loginInfo && loginInfo.token, // 
      username: loginInfo && loginInfo.username, // 
    }),
    // 
    userinfo: fromJS({
      id: -1,
      uuid: '',
      email: '',
      address: '',
      phone: '',
      photo: ''
    }),
    // 
    dealerinfo: fromJS({
      user: null,
      company_name: '',
      review: '',
      address: '',
      phone: '',
      photo: ''
    }),
    // 
    loginModalOpts: fromJS({
      isShow: false, // 
      type: 0 // 01
    }),
    // banners
    banner: fromJS({
      count: 0, // 
      next: null,
      previous: null,
      results: [] // 
    }),
    // cars 
    cars: fromJS({
      count: 0, // 
      next: null,
      previous: null,
      results: [] // 
    }),
    // 
    carInfo: fromJS({
      "id": "",
      "model": {
        "id": -1,
        "subseries": {
          "id": -1,
          "series": {
            "id": -1,
            "brand": {
              "id": -1,
              "name": "",
              "eng_name": null
            },
            "name": ""
          },
          "body_style": {
            "id": -1,
            "value": ""
          },
          "name": "",
          "tank": "",
          "height": -1,
          "length": -1,
          "width": -1,
          "axle": -1
        },
        "transmission": {
          "id": 0,
          "value": ""
        },
        "drivetrain": {
          "id": 0,
          "value": ""
        },
        "fuel": {
          "id": 0,
          "value": ""
        },
        "name": "",
        "engine": "",
        "turbo": false,
        "arrangement": "",
        "year": 0,
        "seating": 0,
        "door": "",
        "msrp": 0,
        "kpl": "",
        "tire": "",
        "interior": null,
        "exterior": null,
        "safety": null,
        "control": null,
        "option": null,
        "url": ""
      },
      "dealer": {
        "user": {
          "id": -1,
          "uuid": "",
          "email": "",
          "address": null,
          "phone": null,
          "photo": null
        },
        "company_name": "",
        "review": "",
        "address": "",
        "phone": "",
        "photo": null
      },
      "vin": "",
      "type": {},
      "exterior_color": {},
      "interior_color": {},
      "is_import": false,
      "photo": [],
      "price": 0,
      "mileage": 0,
      "created_at": "",
      "updated_at": "",
      "sold_at": null,
      "stock": null,
      "description": "",
      "pending": false,
      "available": true,
      "tire": null,
      "interior": [],
      "exterior": [],
      "safety": [],
      "control": [],
      "feature": []
    }),
    // 
    uploadCarInfo: fromJS({
    }),
    // 
    curBrand: fromJS({id: ''}),
    // 
    brands: fromJS({data: []}),
    // 
    curSerie: fromJS({id: ''}),
    // 
    series: fromJS({data: []}),
    // 
    curSubserie: fromJS({id: ''}),
    // 
    subseries: fromJS({data: []}),
    // 
    curModel: fromJS({id: ''}),
    // 
    models: fromJS({data: []})
  },
  /**
   * mutations
   */
  mutations: {
    //  mutations
    setStateData(state, data) {
      const {stateName} = data;
      delete data.stateName;
      state[stateName] = fromJS(data);
    },
    // 
    setLoginInfo(state, loginInfo) {
      state.loginInfo = fromJS(loginInfo);
    },
    // 
    setLoginModalOpts(state, data) {
      state.loginModalOpts = fromJS(data);
    },
    //  banner
    setBanner(state, data) {
      state.banner = fromJS(data);
    }
  },
  /**
   * action
   */
  actions: {
    // 
    signIn({
      commit
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      //  loading 
      commit('setLoginInfo', {isLoading: true});
      // 
      axios({
        method: "post",
        url: `${httpConfig.serverHost}/auth/signin/`,
        transformRequest: [data => Qs.stringify(data)],
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data
      }).then(response => {
        console.log("signIn response", response);
        commit('setLoginInfo', {isLoading: false});
        if (response.status !== 200 && response.status !== 201) {
          vm.$message({message: '', type: 'error', showClose: true});
          return;
        }
        if (response.data.errno !== 0) {
          vm.$message({message: '', type: 'error', showClose: true});
          return;
        }
        vm.$message({message: '', type: 'success', showClose: true});
        //  
        const loginInfo = response.data.data;
        //  loginInfo
        commit('setLoginInfo', loginInfo);
        // 
        commit('setLoginModalOpts', {isShow: false});
        // 
        StoreJs.set(storageConstant.loginInfo, loginInfo);

        // 
        vm
          .$store
          .dispatch("fetchUserInfo", {vm: vm});

        // 
        vm
          .$store
          .dispatch("fetchDealerInfo", {vm: vm});
      }).catch(error => {
        //  loading 
        commit('setLoginInfo', {isLoading: false});
        vm.$message({message: `${error}`, type: 'error', showClose: true});
        console.error(error);
      });
    },
    // 
    signUp({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      //  loading 
      commit('setLoginInfo', {isLoading: true});
      // 
      axios({
        method: "post",
        url: `${httpConfig.serverHost}/auth/signup/`,
        transformRequest: [data => Qs.stringify(data)],
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data
      }).then(response => {
        console.log("signUp response", response);
        commit('setLoginInfo', {isLoading: false});
        if (response.status !== 200 && response.status !== 201) {
          vm.$message({message: '', type: 'error', showClose: true});
          return;
        }
        if (response.data.errno !== 0) {
          vm.$message({message: '', type: 'error', showClose: true});
          return;
        }
        // 
        vm.$message({message: '', type: 'success', showClose: true});
        commit('setLoginModalOpts', {isShow: false});
        // 
        dispatch('signIn', {
          username: data.email,
          password: data.password,
          client_id: httpConfig.client_id,
          grant_type: "password",
          vm: vm
        });
      }).catch(error => {
        console.error('error', error);
        if (error.response.data.errno === 4) {
          //  loading 
          commit('setLoginInfo', {isLoading: false});
          vm.$message({message: ``, type: 'error', showClose: true});
        }
      });
    },
    // 
    logOut({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      //  loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/auth/logout/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("signUp response", response);
        //  loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          commit('setLoginInfo', {isLoading: false});
          vm.$message({message: '', type: 'error', showClose: true});
          return;
        }
        if (response.data.errno !== 0) {
          commit('setLoginInfo', {isLoading: false});
          vm.$message({message: '', type: 'error', showClose: true});
          return;
        }
        // 
        vm.$message({message: '', type: 'success', showClose: true});
        commit('setLoginInfo', {
          token: '',
          username: ''
        });
        // 
        StoreJs.set(storageConstant.loginInfo, null);
      }).catch(error => {
        //  loading
        loading.close();
        console.error('error', error);
      });
    },
    // 
    getIndexAD({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/index_ad/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getIndexAD response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'banner'
        });
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 
    getBrands({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_brands/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getBrands response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '',
          label: '',
          value: ''
        });
        commit('setStateData', {data, stateName: 'brands'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 
    getSeries({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_series/?brand=${data.brandId}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getSeries response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '',
          label: '',
          value: ''
        });
        commit('setStateData', {data, stateName: 'series'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 
    getSubseries({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_subseries/?brand=${data.brandId}&series=${data.seriesId}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getSubseries response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '',
          label: '',
          value: ''
        });
        commit('setStateData', {data, stateName: 'subseries'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 
    getModels({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      console.log('getModels data', data);
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_models/?brand=${data.brandId}&series=${data.seriesId}&subseries=${data.subseriesId}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getModels response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '',
          label: '',
          value: ''
        });
        commit('setStateData', {data, stateName: 'models'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 
    fetchCars({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      // 1
      if (!data.page) {
        data.page = 1;
      }
      console.log('fetchCars data', data);
      let url = `${httpConfig.serverHost}/cars/for_sale/search_result/?page=${data.page}`;
      if (data.brand) {
        url += `&brand=${data.brand}`;
      }
      if (data.series) {
        url += `&series=${data.series}`;
      }
      if (data.subseries) {
        url += `&subseries=${data.subseries}`;
      }
      if (data.model) {
        url += `&model=${data.model}`;
      }
      //  loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
      }).then(response => {
        console.log("fetchCars response", response);
        //  loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'cars'
        });
        window.scrollTo(0, 0);
      }).catch(error => {
        //  loading
        loading.close();
        console.error('error', error);
      });
    },
    // 
    fetchCarInfoById({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      //  loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/detail/${data.id}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("fetchCarInfoById response", response);
        //  loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'carInfo'
        });
      }).catch(error => {
        //  loading
        loading.close();
        console.error('error', error);
      });
    },
    // 
    fetchUserInfo({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      //  token
      const token = vm
        .$store
        .state
        .loginInfo
        .get('token');
      //  loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/mycar/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          "Authorization": `Bearer ${token}`
        }
      }).then(response => {
        console.log("fetchUserInfo response", response);
        //  loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'userinfo'
        });
      }).catch(error => {
        //  loading
        loading.close();
        console.error('error', JSON.stringify(error));
        if (error && error.response && error.response.data && error.response.data.errmsg) {
          vm.$message({message: `${error.response.data.errmsg}`, type: 'error', showClose: true});
        }
      });
    },
    // 
    fetchDealerInfo({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      //  token
      const token = vm
        .$store
        .state
        .loginInfo
        .get('token');
      //  loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/mycar/dealer_info/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          "Authorization": `Bearer ${token}`
        }
      }).then(response => {
        console.log("fetchDealerInfo response", response);
        //  loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'dealerinfo'
        });
      }).catch(error => {
        //  loading
        loading.close();
        console.error('error', JSON.stringify(error));
        if (error && error.response && error.response.data && error.response.data.errmsg) {
          vm.$message({message: `${error.response.data.errmsg}`, type: 'error', showClose: true});
        }
      });
    },
    // 
    uploadCarInfo({
      commit,
      dispatch
    }, data) {
      //  vm 
      const vm = data.vm;
      delete data.vm;
      //  token
      const token = vm
        .$store
        .state
        .loginInfo
        .get('token');
      //  formData
      const formData = new FormData();
      formData.append('type', data.type);
      formData.append('price', data.price);
      formData.append('mileage', data.mileage);
      formData.append('interior_color', data.interior_color);
      formData.append('exterior_color', data.exterior_color);
      formData.append('model_id', data.model_id);
      formData.append('vin', data.vin);
      formData.append('photo', data.photo);
      formData.append('vin_photo', data.vin_photo);
      //  loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "post",
        url: `${httpConfig.serverHost}/mycar/upload_car/`,
        headers: {
          "Content-Type": "multipart/form-data",
          "Authorization": `Bearer ${token}`
        },
        data: formData
      }).then(response => {
        console.log("uploadCarInfo response", response);
        //  loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        vm.$message({message: '', type: 'success', showClose: true});
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'uploadCarInfo'
        });
        //  id
        const carId = data.id;
        // 
        vm.$router.push({
          path: "/cardetail",
          query: {
            carId
          }
        });
      }).catch(error => {
        //  loading
        loading.close();
        console.error('error', JSON.stringify(error));
        if (error && error.response && error.response.data && error.response.data.errmsg && error.response.data.errmsg.type) {
          vm.$message({message: `${error.response.data.errmsg.type}`, type: 'error', showClose: true});
        }
      });
    }
  }
});



