import Vue from 'vue';
import Router from 'vue-router';
import Index from '../containers/Index/Index.vue';
import CarList from '../containers/CarList/CarList.vue';
import LoanApplication from '../containers/LoanApplication/LoanApplication.vue';
import CarDetail from '../containers/CarDetail/CarDetail.vue';
import PersonInfo from '../containers/PersonInfo/PersonInfo.vue';
import PublishCarInfo from '../containers/PublishCarInfo/PublishCarInfo.vue';
import LogisticsOrder from '../containers/LogisticsOrder/LogisticsOrder.vue';
import GarageManage from '../containers/GarageManage/GarageManage.vue';

Vue.use(Router);

export default new Router({
  routes: [
    // 
    {
      path: '/',
      name: 'IndexEmpty',
      component: Index
    },
    // 
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    // 
    {
      path: '/carlist',
      name: 'CarList',
      component: CarList
    },
    // 
    {
      path: '/loanapplication',
      name: 'LoanApplication',
      component: LoanApplication
    },
    // 
    {
      path: '/cardetail',
      name: 'CarDetail',
      component: CarDetail
    },
    // 
    {
      path: '/personinfo',
      name: 'PersonInfo',
      component: PersonInfo
    },
    // 
    {
      path: '/publishcarinfo',
      name: 'PublishCarInfo',
      component: PublishCarInfo
    },
    // 
    {
      path: '/garagemanage',
      name: 'GarageManage',
      component: GarageManage
    },
    // 
    {
      path: '/logisticsorder',
      name: 'LogisticsOrder',
      component: LogisticsOrder
    }
  ],
  // 
  scrollBehavior(to, from, savedPosition) {
    // return  
    return {x: 0, y: 0};
  }
})



